html {
  box-sizing: initial;
}

*, :after, :before {
  box-sizing: inherit;
}

body, h1, h2, h3, h4, h5, h6, p, blockquote, pre, dl, dt, dd, ul, ol, li, fieldset, form, label, table, caption, tbody, tfoot, thead, tr, th, td {
  margin: 0;
  padding: 0;
}

.wrapper {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__minute-hand {
  background-color: #0700ff;
  width: 20vmin;
  height: 3vmin;
  animation-name: hand-move;
  animation-duration: 3600s;
  animation-timing-function: steps(60, end);
  animation-iteration-count: infinite;
  position: absolute;
  top: 49%;
  left: 50%;
  transform: translate(-50%, -10vmin)rotate(-90deg);
}

.stopwatch__seconds-hand {
  background-color: #2c8000;
  width: 38vmin;
  height: 1.5vmin;
  animation-name: hand-move;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  top: 49%;
  left: 50%;
  transform: translate(-50%, -19vmin)rotate(-90deg);
}

.stopwatch:after {
  content: "";
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 49%;
  left: 50%;
  transform: translate(-50%, -1.5vmin);
}

.stopwatch--speed-up .stopwatch__minute-hand {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__seconds-hand {
  animation-duration: 10s;
}

@keyframes hand-move {
  from {
    transform-origin: 0;
    transform: translate(0)rotate(-90deg);
  }

  to {
    transform-origin: 0;
    transform: translate(0)rotate(270deg);
  }
}

